import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home_'
import Projects from '@/components/Projects'
import read_me from '@/components/ReadMe'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/:id',
      name: 'Projects',
      component: Projects
    },
    {
      path: '/:id/:proj',
      name: 'ReadMe',
      component: read_me
    }
  ]
})



