<template>
<div>
  <h2>Welcome to {{proj}}'s ReadMe file</h2>
  <div class="readme" v-if="readme" v-html="readme">
  </div>
  <div v-else>Fetching ReadMe</div>
</div>
</template>

<script>
import '@/bootstrap.js'
export default {
  name: 'read_me',
  data () {
    return {
      username: null,
      proj: null,
      readme: null
    }
  },
  created() {
    this.username = this.$route.params.id;
    this.proj = this.$route.params.proj;
    this.fetchReadMe(this.username, this.proj);
  },
  methods: {
    fetchReadMe (username, project_name){
      this.$http.get(`https://api.github.com/repos/${username}/${project_name}/readme`)
        .then(({data}) => {
            this.readme = data;
    })
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div {
    width: 90%; 
    padding: 10px;
    text-align: center;
    margin: auto;
}
div.readme{
  word-wrap: break-word;
    border: 1px solid #000000;
}
</style>


